.widgetLg{
    flex: 2;
    -webkit-box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.12); 
    box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.12);
    padding: 20px;
    margin-right: 20px;
}
.widgetLgTitle{
    font-size: 22px;
    font-weight: 600;
}
.widgetLgTable{
    width: 100%;
    border-spacing: 20px;
}
.widgetLgTh{
    text-align: left;
}
.widgetLgUser{
    display: flex;
    align-items: center;
    font-weight: 600;
}
.widgetLgImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.widgetLgDate, .widgetLgAmount{
    font-weight: 300;
}
.widgetLgButton{
    padding: 5px 7px;
    border: none;
    border-radius: 5px;   
}
.widgetLgButton.Approved{
    background-color:#e5faf2;
    color: #3bb077;
}
.widgetLgButton.Declined{
    background-color:#fff0f1;
    color: #d95087;
}
.widgetLgButton.Pending{
    background-color:#ebf1fe;
    color: #2a7ade;
}