.user{
    flex: 4;
    padding: 20px;
}
.userTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.userAddButton{
    width: 80px;
    border: none;
    background-color: teal;
    border-radius:5px;
    color:white;
    padding: 5px;
    font-size: 16px;
}
.userContainer{
    display: flex;
    margin-top: 20px;
}
.userShow{
    
    flex: 1;
    padding: 20px;
    margin-right: 20px;
    -webkit-box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.12); 
box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.12);
}
.userUpdate{
    flex: 2;
    padding: 20px;
    -webkit-box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.12); 
box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.12);
}
.userShowImg{
    height: 40px;
    width: 40px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 10px;
   
}
.userShowTop{
    display: flex;
    align-items: center;
}

.userTopTitle{
    display: flex;
    flex-direction: column;
}
.userShowUsername{
    font-weight: 600;
}
.userShowTitle{
    font-weight: 300;
}
.userShowTitle{
    font-size: 14px;
    font-weight: 600;
    color: lightgray;
    
}
.userShowBottom{
    margin-top: 20px;
}
.userShowInfo{
    display: flex;
align-items: center;
margin: 20px 0;
color:#444;
}
.userShowIcon{
    font-size: 16px !important;
}
.userShowInfoTitle{
    margin-left: 10px;
}
.userUpdateImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 10px;
}
.userUpdateTitle{
    font-size: 24px;
    font-weight: 600;
}
.userUpdateItem>label{
    margin-bottom: 5px;
    font-size: 14px;
}
.userUpdateInput{
    width: 250px;
    height: 30px;
    border: none;
    border-bottom: 1px solid gray;
}
.userUpdateItem{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}
.userUpdateForm{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}
.userUpdateRight{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.userUpdateUpload{
    display: flex;
    align-items: center;
    
}

.userUpdateImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 10px;
}
.userUpdateIcon{
 cursor: pointer;   
}
.userUpdateButton{
    border-radius: 5px;
    border:none;
    padding: 5px;
    cursor: pointer;
    background-color: darkblue;
    color:white;
    font-weight: 600;
}

